<template>
    <section class="layout">

        <h1 class="title" v-if="$route && $route.name === 'catalog'">{{categoryId}}</h1>
        <h1 class="title" v-if="$route && $route.name === 'search'">"{{searchRequest}}"</h1>

        <div v-if="($route.name === 'catalog' || $route.name === 'catalogWithSub' || isComponent) && products.length !== 0 && !isLoading"
             class="search-result-wrapper" id="grid" v-bind:style="{'margin-bottom': searchResultWrapperMarginBottom}">

            <Card v-for="item in products" :key="item.id"
                  :id="item.id" :name="item.name"
                  :options="item.options" :children-data="item.childrenData"
                  :button-text="componentData ? componentData.buttonText : 'Заказать'" v-on:buy="onBuy">
            </Card>
        </div>

        <div v-if=" ($route.name === 'search' || isComponent) && (lastSearchRequest === searchRequest || lastSearchRequest === componentData.searchRequest) && !isLoading"
             class="search-result-wrapper" id="grid" v-bind:style="{'margin-bottom': searchResultWrapperMarginBottom}">

            <Card v-for="item in searchResults" :key="item.id"
                  :id="item.id" :name="item.name"
                  :options="item.options" :children-data="item.childrenData"
                  :button-text="componentData ? componentData.buttonText : 'Заказать' " v-on:buy="onBuy">
            </Card>

        </div>

        <div class="spinner">
            <LoadingSpinner v-if="isLoading"></LoadingSpinner>
        </div>

        <!--Basket -->
        <Basket v-bind:data="!isComponent ? basket: basketData"
                v-bind:can-be-ordered="!isComponent"
                @ordering="onOrdering" @remove="onRemove">
        </Basket>

    </section>
</template>

<script>
    import { mapGetters, mapActions, mapMutations } from 'vuex';
    import modules from "../store/all.modules.name"
    import actions from "../store/all.actions.type";
    import mutations from "../store/all.mutations.type";

    import Card from "./components/catalog/Card";
    import LoadingSpinner from "./components/LoadingSpinner";

    export default {
        name: "Catalog",

        components: {
            LoadingSpinner,
            Basket: () => import("./components/catalog/Basket"),
            Card},

        data: () => {
            return {
                isLoading: true,
            }
        },

        props: {
            isComponent: {
                type: Boolean,
                default: false,
            },

            componentData: {
                type: Object,
                default: null,
            },

            basketData: {
                type: Array,
                default: null,
            },
        },

        methods: {
            ...mapActions(modules.catalog, [
                actions.catalog.ADD_TO_BASKET,
                actions.catalog.GET_PRODUCTS,
                actions.catalog.SEARCH,
            ]),

            ...mapMutations(modules.catalog, [
                mutations.catalog.REMOVE_FROM_BASKET,
                mutations.catalog.SET_IS_BASKET_MINIMIZED,
            ]),

            init() {
                console.log("init()");
                this.isLoading = true;

                if (!this.isComponent) {
                    if (this.$route.name === "catalog" || this.$route.name === "catalogWithSub") {
                        this.getProducts({categoryId: this.categoryId, subCategoryId: this.subCategoryId}).then(() => {
                            this.isLoading = false;
                        })
                    } else if (this.$route.name === "search") {
                        this.search(this.searchRequest).then(() => {
                            this.isLoading = false;
                        })
                    }
                } else {

                    if ( this.$props.componentData.category.length !== 0) {

                        if (this.$props.componentData.subCategory.length !== 0) {
                            this.getProducts({categoryId: this.$props.componentData.category, subCategoryId: this.$props.componentData.subCategory})
                                .then(() => {
                                    this.isLoading = false;
                                })
                        } else {
                            this.getProducts({categoryId: this.$props.componentData.category}).then(() => {
                                this.isLoading = false;
                            })
                        }

                    } else if (this.$props.componentData.searchRequest.length > 0) {
                        this.search(this.$props.componentData.searchRequest).then(() => {
                            this.isLoading = false;
                        })
                    }
                }
            },

            onBuy(item) {
                this.setIsBasketMinimized(false);

                if (!this.isComponent) {
                    this.addToBasket(item)
                } else {
                    this.$emit('buy', item);
                }
            },

            onOrdering() {
                this.$router.push({name: "ordering"})
            },

            onRemove(index) {
                if (!this.isComponent) {
                    this.removeFromBasket(index);
                } else {
                    this.$emit('basketRemove', index)
                }
            }
        },

        computed: {
            ...mapGetters(modules.catalog, [
                "products",
                "basket",
                "lastChosenCategory",
                "lastSearchRequest",
                "searchResults",
            ]),

            searchResultWrapperMarginBottom: function() {
                if (this.isBaskeWindowVisible) {
                    return 230 + "px"
                } else {
                    return 0 + "px"
                }
            },

            categoryId: function () {
                if (this.$route && this.$route.params && (this.$route.name === "catalog" || this.$route.name === "catalogWithSub")) {
                    return this.$route.params.categoryId;
                } else {
                    return null;
                }
            },

            subCategoryId: function () {
                if (this.$route && this.$route.params && this.$route.name === "catalogWithSub") {
                    return this.$route.params.subCategoryId;
                } else {
                    return "";
                }
            },

            searchRequest: function () {
                if (this.$route && this.$route.params && this.$route.name === "search") {
                    return this.$route.params.request;
                } else {
                    return ""
                }
            },
        },

        watch: {
            '$route': {
                handler: function() {
                    this.init();
                },
                deep: true,
                immediate: true
            }
        },

        mounted() {
            //this.init();
        },

        destroyed() {
        }
    }
</script>

<style lang="sass" scoped>
    @import '../assets/basic'

    .layout
        width: 90%!important
        margin: auto
        max-width: 1200px!important
        font-family: Roboto, sans-serif

        .title
            text-align: center
            font-size: 1.8rem
            font-weight: normal
            font-family: Oswald, sans-serif

        .spinner
            display: flex
            justify-content: center
            margin-top: 2rem


        .search-result-wrapper
            display: grid
            grid-gap: 15px
            grid-template-columns: repeat( auto-fill, minmax(250px, 1fr) )

            /*@include mobile
                grid-template-columns: 1fr

            /*@include tablet
                grid-template-columns: repeat(2, 1fr)

            /*@include laptop
                grid-template-columns: repeat(3, 1fr)

            /*@include laptop-l
                grid-template-columns: repeat(4, 1fr)
</style>