<template>
    <!--v-bind:class="{'card-minified': !currentOption || !currentOption.hasImage}"-->
    <div class="card">
        <div class="header">
            <p class="title" v-bind:class="{'title-minified': name.length > 45}">{{name}}</p>

            <section class="title-sub-container" v-if="optionsLayers.length !== 0">

                <section class="option" v-for="(item, index) in optionsLayers" :key="index">
                    <SimpleDroplist v-if="item.length > 1" :data="item" v-on:choose="changeChosenOption(index, $event)">
                    </SimpleDroplist>
                    <p v-else>{{item[0]}}</p>
                </section>

            </section>
        </div>

        <LazyLoadImage
                class="main-image" v-on:click="imageHandler"
                v-bind:url="currentImage"
        />

        <!--<img class="main-image" :src="currentImage" v-on:click="imageHandler">-->

        <div class="content" v-if="lastOptionLayer.data.length !== 0" v-bind:style="cardContentStyle">

            <div class="option" v-for="(size, index) in lastOptionLayer.data" :key="size.name">
                <p class="name" v-bind:class="{chosen: index === lastOptionLayer.chosenIndex}"
                   v-on:click="lastOptionLayer.chosenIndex = index">
                    {{size.name}}
                </p>

                <p class="quantity" v-bind:class="{'chosen-quantity': index === lastOptionLayer.chosenIndex}"
                   v-on:click="lastOptionLayer.chosenIndex = index">
                    {{childrenData[size.id].isCountInStockTooBig?
                    ('>' + childrenData[size.id].countInStock) :
                    childrenData[size.id].countInStock}} <span>шт.</span>
                </p>
            </div>

        </div>

        <div class="bottom">
            <MDRaisedButton v-bind:text="buttonText" v-on:click="buyHandler()">
            </MDRaisedButton>

            <!--<button v-on:click="buyHandler()">
                   Заказать
            </button>-->

            <p class="price" v-if="optionsLayers.length !== 0">{{currentOption.priceRetail}} ₴</p>
        </div>

    </div>
</template>

<script>
    import SimpleDroplist from "./SimpleDroplist";
    import {EventBus, Events} from "../../../common/eventBus";
    import imageFetch from "../../../common/imageFetch";
    import MDRaisedButton from "../MD/MDRaisedButton";
    import LazyLoadImage from "../lazyLoadImage/LazyLoadImage";

    export default {
        name: "CatalogCard",

        components: {LazyLoadImage, MDRaisedButton, SimpleDroplist},

        props: {
            id : {
                type: Number,
                required: true,
            },

            name: {
                type: String,
                required: true,
            },

            options: {
                type: Array,
                required: true,
            },

            childrenData: {
                type: Object,
                required: true,
            },

            buttonText: {
                type: String,
                required: true
            },
        },

        data: () => {
            return {
                route: [],
                optionsLayers: [],
                eventBus: null,

                lastOptionLayer: {
                    chosenIndex: 0,
                    data: [],
                },

                cardContentStyle: {
                    gridTemplateColumns: 'repeat(auto-fit, minmax(' + 30 + 'px, max-content))',
                }
            }
        },

        methods: {
            changeChosenOption(indexInLayers, chosenIndex) {
                this.route[indexInLayers] = chosenIndex;
                this.reCalculateOptionsLayer();
            },

            imageHandler() {
                this.eventBus.$emit(Events.showImagePreview, imageFetch.fullSizeImageURL(this.currentOption.id))
            },

            buyHandler() {
                this.$emit("buy", {
                    id : this.currentOption.id,
                    name: this.currentOption.fullName,
                    //size: this.currentOption.size,
                    //color: this.currentOption.color,
                    hasImage: this.currentOption.hasImage,
                    price: this.currentOption.priceRetail,
                });
            },

            reCalculateOptionsLayer() {
                let res = [],
                    lastLayer = [],
                    currentLayer = this.options;


                for (let i = 0; i < this.route.length + 1; i++) {

                    if (i !== this.route.length) {
                        let temp = [];

                        currentLayer.forEach((item) => {
                            temp.push(item.name);
                        });

                        res.push(temp);

                        if (currentLayer[this.route[i]]) {
                            currentLayer = currentLayer[this.route[i]].options;
                        } else {
                            currentLayer = currentLayer[0].options;
                            this.options[i] = 0;
                        }

                    } else {
                        lastLayer = currentLayer;
                        if (this.lastOptionLayer.chosenIndex > (lastLayer.length - 1)) {
                            this.lastOptionLayer.chosenIndex = 0;
                        }
                    }
                }

                this.optionsLayers = res;
                this.lastOptionLayer.data = lastLayer;
                this.reCalculateMaxOptionWidth();
            },

            calculateOptionsLayer() {
                let res = [],
                    lastLayer = [],
                    tempRoute = [],
                    currentLayer = this.options;

                while (true) {
                    if (currentLayer[0].options && currentLayer[0].options.length > 0) {
                        let temp = [];

                        currentLayer.forEach((item) => {
                            temp.push(item.name);
                        });

                        res.push(temp);
                        tempRoute.push(0);

                        currentLayer = currentLayer[0].options;
                    } else {
                        lastLayer = currentLayer;
                        break
                    }
                }

                this.optionsLayers = res;
                this.route = tempRoute;
                this.lastOptionLayer.data = lastLayer;
                this.reCalculateMaxOptionWidth();
            },

            reCalculateMaxOptionWidth() {
                let max = 0,
                    coef = 9;

                if(!this.lastOptionLayer || !this.lastOptionLayer.data)
                    return;

                this.lastOptionLayer.data.forEach((item) => {
                    if (item.name.length * coef > max) {
                        max = item.name.length * coef;
                    }
                });

                this.cardContentStyle.gridTemplateColumns = 'repeat(auto-fit, minmax(' + max + 'px, max-content))';
            },
        },

        computed: {
            currentImage: function () {

                if (this.hasImage) {
                    return imageFetch.imageURL(this.currentOption.id);
                } else {
                    return "";
                }

                //return "https://s-cdn.sportbox.ru/images/styles/upload/fp_fotos/22/91/d815718cb0c678463c61276440025c00598b606041e1b103979205.jpg"
            },

            hasImage: function() {
                return this.currentOption && this.currentOption.hasImage
            },

            currentOption: function() {
                if (this.lastOptionLayer.data.length === 0) {
                    return
                }
                return this.childrenData[this.lastOptionLayer.data[this.lastOptionLayer.chosenIndex].id];
            },
        },

        mounted() {
            this.eventBus = EventBus;
            this.calculateOptionsLayer();
        },
    }
</script>

<style lang="sass" scoped>
    @import '../../../assets/basic'

    .card-minified
        grid-template-rows: 80px auto 50px!important

    .card
        display: grid
        grid-template-rows: 80px 210px auto 50px
        //transition: 0.35s
        background-color: $background-color
        box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.12), 0 1px 4px 0px rgba(0, 0, 0, 0.24);
        transition: all 0.3s cubic-bezier(.25,.8,.25,1)
        border-radius: 5px
        //grid-template-columns: 63px 1fr
        padding: 10px 10px

        @include higher-then($mobile)
            &:hover
                box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0px 1px 6px rgba(0, 0, 0, 0.23)
                //box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.5)

        p
            display: inline-block
            margin: 0

        .main-image
            display: block
            max-width: 100%
            margin: 5px auto
            height: 200px
            cursor: pointer

        .header
            height: 80px
            //overflow: hidden
            display: flex
            flex-direction: column
            justify-content: space-around

            .title
                max-height: 85px
                padding-bottom: 6px
                overflow: hidden
                display: block
                position: relative
                font-size: 1.1rem
                line-height: 1
                text-align: center

                &:after
                    display: block
                    position: absolute
                    width: 100%
                    bottom: 0
                    content: ""
                    background: linear-gradient(to bottom, rgba(255,255,255,0), $background-color)
                    height: 5px

            .title-minified
                font-size: 0.9rem!important
                line-height: 1.1!important


            .title-sub-container
                display: flex
                justify-content: space-around

                .option
                    p
                        max-height: 20px
                        overflow: hidden
                        display: block
                        text-align: center
                        color: #777777
                        font-size: 0.9em

        .content
            border-top: 2px solid #777777
            margin-top: 10px
            display: grid
            grid-template-columns: repeat(auto-fit, 45px)
            row-gap: 20px
            align-items: center
            justify-content: space-around
            padding-left: 10px
            padding-right: 10px
            padding-top: 10px

            .option
                display: grid
                grid-template-rows: auto auto
                text-align: center
                align-items: center
                justify-content: center

                .name
                    cursor: pointer
                    padding: 1px 2px
                    color: #bababa
                    transition: 0.2s

                .chosen
                    //background-color: $secondary-color
                    //color: $background-color
                    color: black
                    cursor: unset

                .chosen-quantity
                    color: black!important
                    cursor: unset!important

                .quantity
                    cursor: pointer
                    color: #bababa
                    font-size: 0.9em
                    transition: 0.2s

                    span
                        font-size: 0.8em

        .bottom
            display: flex
            align-items: center
            margin-top: 20px

            .price
                margin-left: auto

            button
                display: block
                max-width: 120px
                width: 80%
                background-color: $secondary-color
                color: $background-color
                font-size: 16px
                padding-top: 5px
                padding-bottom: 5px
                border: none
                border-radius: 5px

                &:hover
                    cursor: pointer

                &:focus
                    outline: none
</style>